.header-wrapper {
  background-color: #C6C7C8;
}
.header__logo {
  align-self: baseline;
  color: #222222;
}
.header__search-button {
  border-color: #A3A3A4;
  color: #222222;
}
.header__search-button:hover {
  border-color: #848484;
  background-color: #949595;
}
.header__search-button:focus {
  background-color: #949595;
  box-shadow: 0 0 0 0.25rem rgba(58, 58, 58, 0.5);
  border-color: #848484;
}
.header__search-button:active {
  background-color: #7F7F7F;
  border-color: #6A6B6B;
}
.header__search-button:active:focus {
  box-shadow: none;
}
.header__search-input {
  min-width: 190px;
}
.site-list {
  flex-wrap: wrap;
}
.site-list__link {
  color: #222222;
}
.main-wrapper {
  padding-top: 20px;
  padding-bottom: 20px;
  background-color: #f1f1f1;
  background-image: url("../img/main-background.jpg");
  background-repeat: no-repeat;
  background-size: 100%;
}
.main__title {
  text-align: center;
  color: #222222;
}
.products__title {
  text-align: center;
  color: #222222;
  padding: 20px 0;
}
.products__example .products__example-button {
  filter: none;
}
.other-products {
  padding: 20px 0;
}
.other-products__title {
  text-align: left;
  color: #222222;
  padding: 20px 0;
}
.footer__wrapper {
  background-color: #C6C7C8;
  min-height: 70px;
}
.footer__contacts {
  min-height: 70px;
}
.footer__logo {
  align-self: baseline;
  color: #222222;
  font-weight: 700;
}
.footer__address {
  text-align: center;
}
.footer-phone {
  text-align: right;
}
.footer__phone-number {
  color: #222222;
  text-decoration: none;
}
